<template>
  <div>
    <b-card>
      <h1>{{selectedItem.date}}</h1>
      <div v-for="(item, index) in selectedItem.content" :key="index">
        <transition name="test" mode="out-in">
          <template v-if="youtube_parser(item)">
            <youtube :video-id="youtube_parser(item)" ref="youtube"></youtube>
          </template>
          <template v-else>
            <card :siteData="item"/>
          </template>
        </transition>
      </div>
    </b-card>
  </div>
</template>
<script>
import Card from "./Card"

export default {
  name: 'Detail',
  props:{
    selectedItem: {
      type: Object,
      default: null,
    }
  },
  components: {
    Card
  },
  methods:{
    youtube_parser(item){
      var regExp = /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#\&\?]*).*/;
      var match = item.match(regExp);
      return (match&&match[7].length==11)? match[7] : false;
    }
  }
}
</script>
<style>
.test-enter-active,
.test-leave-active{
  transition: all .3s;
}
.test-enter, .test-leave-to{
  opacity:0;
  transform: translateY(-500px);
}
</style>