<template>
  <div class="card" style="width: 40rem;" v-if="siteMeta">
    <img class="card-img-top" :src="siteMeta.image.url" :alt="siteMeta.title">
    <div class="card-block">
      <h4 class="card-title">{{siteMeta.title}}</h4>
      <p class="card-text">{{siteMeta.description}}</p>
      <a v-bind:href="siteMeta.url" class="btn btn-primary">More</a>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "Card",
  data() {
    return {
      siteMeta: null
    };
  },
  props: {
    siteData: {
      type: String,
      default: null
    }
  },
  mounted() {
    if (this.siteData) {
      this.getData(this.siteData);
    }
  },
  methods: {
    getData(link) {
      axios.get(
        'https://api.microlink.io/',
        {
          params: {
            url: link,
          }
        }).then(response => {
          console.log(response);
          this.siteMeta = response.data.data;
        });
    },
  }
};
</script>
<style>
.card{
  margin-left: auto;
  margin-right: auto;
}
</style>