<template>
  <div>
    <div class="list">
      <b-row>
        <b-col>
          <h1 class="phrase1">
            {{ phrase }}
          </h1>
        </b-col>
        <b-col cols="7">
          <List v-on:onSelect="onSelectChange($event)"/>
        </b-col>
        <b-col>
          <h1 class="endPhrase">
          {{endPhrase}}
          </h1>
        </b-col>
      </b-row>
    </div>
    <Detail :selectedItem="selectedItem"  v-if="selectedItem"/>
  </div>
</template>

<script>
import List from './List.vue'
import Detail from './Detail.vue'

export default {
  name: 'Spacex',
  components: {
    List,
    Detail
  },
  methods:{
    onSelectChange(value){
      this.selectedItem= value.title ? value : null;
    }
  },
  data(){
    return {
      phrase: 'Вот когда',
      endPhrase: ',тогда и поговорим',
      selectedItem: null,
    
    }
  }
}
</script>

<style>
.list {
  display: flex;
}
.phrase1, .endPhrase{
  text-align: center;
}
</style>