<template>
  <b-list-group >
    <template v-for="(item, index) in list">
      <transition name="test" mode="out-in">
        <b-list-group-item
          v-on:click="onselect(item)"
          v-if="showItem(item)"
          :key="index"
        >
          {{ item.title }}
        </b-list-group-item>
      </transition>
    </template>
  </b-list-group>
</template>
<script>
import listItems from '../data.js'

export default {
  name: 'List',
  data() {
    return {
      phrase: 'Вот когда',
      list: listItems,
      show:true,
      selectedItem2: {},
    }
  },
  methods:{
    onselect(element) {
      if (this.selectedItem2.title === element.title) {
        this.selectedItem2 = {};
      } else {
        this.selectedItem2 = element;
      }
      this.$emit('onSelect', this.selectedItem2)
    },
    showItem(item) {
      if (!this.selectedItem2.title) {
        return true;
      }
      return this.selectedItem2.title === item.title;
    }
  }
}
</script>
<style>
.test-enter-active,
.test-leave-active{
  transition: all .3s;
}
.test-enter, .test-leave-to{
  opacity:0;
  transform: translateY(-500px);
}
</style>